 import {
 	combineReducers
 } from 'redux';

 import moment from 'moment';

 // -- 

 import {
 	Param
 } from './listParam';

 // -- 
 import {
 	listTable
 } from './listTable';

 //
 import {
 	addUser
 } from './addUser';



 const userList = combineReducers({
 	Param,
 	listTable,
 	addUser
 });


 export {
 	userList
 }


