/**
 * table
 * @method Table 
 * @param state {Object} 
 * @param action {Object} 
 */

const listTable = (state, action) => {

	//
	if (typeof state === "undefined") {
		//
		return {
			columns: [{
				title: '',
				dataIndex: 'name',
				key: 'name',
			}, {
				title: '',
				dataIndex: 'email',
				key: 'email',
			}, {
				title: '',
				dataIndex: 'uid',
				key: 'uid'
			}],
			result: [],
			total: 1
		};
	}



	switch (action.type) {

		case "USER_LIST_COLUMNS":
			//
			return Object.assign({}, state, {
				columns: [{
					title: '',
					dataIndex: 'name',
					key: 'name',
				}, {
					title: '',
					dataIndex: 'email',
					key: 'email',
				}, {
					title: '',
					dataIndex: 'uid',
					key: 'uid'
				}]
			});

		case "USER_LIST_RESULT":
			//
			return Object.assign({}, state, {
				result: action.payload
			});


		case "USER_LIST_TOTAL":
			//
			return Object.assign({}, state, {
				total: action.payload
			});
		default:
			//
			return state;

	}


}

export {
	listTable
}


