 /**
  * 
  * @method Result
  * @param state 
  * @param action 
  * @param state 
  */
 const Result = (state, action) => {

 	//,
 	if (typeof state === "undefined") {
 		return {
 			tableList: [],
 			excelData: []
 		};
 	}

 	switch (action.type) {
 		//
 		case "PROFILE_LIST_TABLE":
 			return Object.assign({}, state, {
 				tableList: action.payload
 			});
 		case "PROFILE_LIST_EXCEL":
 			return Object.assign({}, state, {
 				excelData: action.payload
 			});
 		default:
 			return state;
 	}
 }

 export {
 	Result
 }


