/**
 * 
 * @method ChannelTypeList
 * @param state {Object} 
 * @param action {Object} 
 */


const ChannelTypeList = (state, action) => {
    //
    if (typeof state === "undefined") {
        return {
            channelTypeList: []
        };
    }

    switch (action.type) {
        case "CHANNELLIST_CHANNELTYPELIST":
            return Object.assign({}, state, {
                channelTypeList: action.payload
            });
        default:
            return state;
    }
}

export {
    ChannelTypeList
}


