import React from 'react';
/**
 * table 
 * @method TableColumns
 * @param state {Object} 
 * @param action {Object} 
 */

const TableColumns = (state, action) => {
	if (typeof state === "undefined") {
		//
		return {
			tableColumns: [{
				title: '',
				dataIndex: 'day'
			}, {
				title: '',
				dataIndex: 'newUser'
			}, {
				title: '',
				dataIndex: 'qualityPer',
				render: (text) => (<span> {(Number(text) * 100).toFixed(2) + '%'} </span>)
			}, {
				title: '',
				dataIndex: 'activeUser'
			}, {
				title: '',
				dataIndex: 'visitUser'
			}, {
				title: '',
				dataIndex: 'loseUser'
			}, {
				title: '',
				dataIndex: 'backUser'
			}, {
				title: '',
				dataIndex: 'outUser'
			}]
		};
	}

	switch (action.type) {
		case "PROFILELIST_TABLE_COLUMNS":
			return Object.assign({}, state, {
				tableColumns: action.payload
			});
		default:
			return state;
	}
}

export {
	TableColumns
}


