/**
 * 
 * @method ChannelGroupList
 * @param state {Object} 
 * @param action {Object} 
 */


const ChannelGroupList = (state, action) => {
    //
    if (typeof state === "undefined") {
        return {
            channelGroupList: []
        };
    }

    switch (action.type) {
        case "CHANNELLIST_CHANNELGROUPLIST":
            return Object.assign({}, state, {
                channelGroupList: action.payload
            });
        default:
            return state;
    }
}

export {
    ChannelGroupList
}


