/**
 * 
 * @method Param
 * @param state {Object} 
 * @param action (Object) 
 */
const Param = (state, action) => {
	if (typeof state === "undefined") {
		//
		return {
			offset: 1,
			limit: 10,
			name: "",
			loading: false
		};
	}

	switch (action.type) {

		case "USER_LIST_OFFSET":
			//
			return Object.assign({}, state, {
				offset: action.payload
			});

		case "USER_LIST_LIST":
			//
			return Object.assign({}, state, {
				limit: action.payload
			});

		case "USER_LIST_NAME":
			//
			return Object.assign({}, state, {
				name: action.payload
			});
		case "USER_LIST_LOADING":
			//
			return Object.assign({}, state, {
				loading: action.payload
			});
		default:
			//
			return state;

	}
}

export {
	Param
}


