/**
 * 
 * @method Result
 * @param state 
 * @param action 
 * @param state 
 */
const Result = (state, action) => {

    //,
    if (typeof state === "undefined") {
        return {
            tableList: []
        };
    }

    switch (action.type) {
        //
        case "CHANNEL_LIST_TABLE":
            return Object.assign({}, state, {
                tableList: action.payload
            });
        default:
            return state;
    }
}

export {
    Result
}


