/**
 * email 
 * @method uniqueUser
 * @param state {Object} 
 * @param action (Object) 
 */
const addUser = (state, action) => {
	if (typeof state === "undefined") {
		//
		return {
			email: "",
			name: "",
			auth: []
		};
	}

	switch (action.type) {

		case "CREATE_USER_EMAIL":
			// 
			return Object.assign({}, state, {
				email: action.payload
			});

		case "CREATE_USER_NAME":
			// 
			return Object.assign({}, state, {
				name: action.payload
			});

		case "CREATE_USER_AUTH":
			// 
			return Object.assign({}, state, {
				auth: action.payload
			});

		default:
			//
			return state;
	}
}

export {
	addUser
}


